* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #feefec;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
}

.container {
  grid-template-columns: 3fr 2fr;
  justify-content: center;
  align-self: center;
  gap: 2rem 1rem;
  max-width: 90rem;
  margin: 0 auto;
  display: grid;
}

label {
  font-size: 1.8rem;
  font-weight: 600;
}

.display-none {
  display: none !important;
}

.display-hidden {
  opacity: 0;
}

.grid-2-cols {
  grid-template-columns: repeat(3, 1fr);
}

.section-cube {
  grid-column: 1 / -1;
  justify-content: space-evenly;
  justify-self: center;
  align-items: center;
  gap: 3rem;
  width: 100%;
  height: max-content;
  padding: 1.5rem 1.5rem 0;
  display: flex;
  overflow-x: auto;
}

.section-cube img {
  width: 4rem;
  height: 4rem;
}

.cube-div img {
  cursor: pointer;
  transition: all .2s;
}

.cube-div img:hover {
  scale: 1.15;
}

.count-num {
  text-align: center;
  font-size: 2rem;
}

.section-play {
  justify-content: center;
  display: flex;
}

.play {
  background-color: #fac0b2;
  border-radius: 9px;
  align-content: space-between;
  gap: 1rem;
  width: 38rem;
  height: max-content;
  padding: 2rem;
  font-size: 1.8rem;
  display: grid;
  box-shadow: 0 1px 3px #0000004d;
}

.play button {
  color: #fff;
  cursor: pointer;
  background-color: #f2613f;
  border: none;
  border-radius: 5px;
  justify-content: center;
  justify-self: center;
  width: 45%;
  padding: .8rem 1.6rem;
  font-size: 2rem;
  display: flex;
  box-shadow: 0 0 1px 1px #0000004d;
}

.play button:hover {
  background-color: #da5739;
  box-shadow: 0 0 1.5px 1px #00000080;
}

.play button:active {
  background-color: #c24e32;
  box-shadow: 0 0 2px 1px #00000080;
}

.section-play h3 {
  margin-bottom: 2rem;
}

.section-play h3, .section-play h4 {
  text-align: center;
}

.btn-div {
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.pot-div {
  box-shadow: 0px 1px 3px 3px hsla (0, 0%, 0%, .3);
  background-color: #feefec;
  border-radius: 7px;
  flex-direction: column;
  justify-content: center;
  min-height: 15rem;
  padding: 1rem;
  transition: all .3s;
  display: flex;
}

.play-hexa .pot-div {
  justify-content: flex-start;
  min-height: 20rem;
}

.part-before:hover, .part-after:hover {
  cursor: pointer;
  background-color: #e5d7d4;
  box-shadow: 0 1px 3px #0000004d;
}

.play-return .part-before p:hover {
  background-color: #cbbfbd;
}

.play-hexa .pot-div {
  cursor: pointer;
}

.shine-lv-up {
  justify-content: center;
  display: flex;
}

.fixed-pot, .chosen {
  background-color: #cbbfbd;
}

.section-display {
  color: #ddd;
  background-color: #00000080;
  border-radius: 9px;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 2rem;
  height: max-content;
  padding: 2rem;
  display: flex;
}

.display-option {
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.section-display select {
  color: #fff;
  background-color: #f2613f;
  border-radius: 5px;
  width: 8rem;
  padding: .4rem;
  font-size: 1.4rem;
}

.btn-init {
  color: #fff;
  cursor: pointer;
  background-color: #f2613f;
  border: none;
  border-radius: 5px;
  justify-content: center;
  justify-self: center;
  width: 45%;
  padding: .8rem 1.6rem;
  display: flex;
  box-shadow: 0 0 1px 1px #0000004d;
}

.btn-init:hover {
  background-color: #da5739;
  box-shadow: 0 0 1.5px 1px #00000080;
}

.btn-init:active {
  background-color: #c24e32;
  box-shadow: 0 0 2px 1px #00000080;
}
